<script>
    export default{
        methods:{
            getResume(){
                window.open('../assets/resume.pdf');
            },
            swapLang(){
                if(this.lang == 'english'){
                    this.$store.state.lang = 'japanese';
                }
                else if(this.lang == 'japanese'){
                    this.$store.state.lang = 'english';
                }
            }
        },
        computed:{
            lang(){
                return this.$store.state.lang;
            }
