import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        lang: 'english'
    },
    mutations:{
        updateLang: (state, payload) => {
            if(payload == "japanese" || payload == "english"){
                state.lang = payload;
            }
        },
    },
    actions:{
        changeLang: (context, payload) => {
            context.commit('updateLang', payload);
        }
    }
})